.home {
    display: flex;
    justify-content: center;
    margin-left: 0;
    margin-right: 0;
    

  }
  .home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  }
  .home__row {  
    display: flex; 
    z-index: 1; 
    margin-left: 5px;  
    margin-right: 5px;
  }
  
  
  .checkbox__container {
    padding: 0;
    list-style: none;
    background: transparent;
}
.checkbox__container li {
    display: inline-block;
    position: relative;
    line-height: 21px;
    text-align: left;
}
.checkbox__container li a {
    display: block;
    padding: 8px 25px;
    color: #333;
    text-decoration: none;
}
.checkbox__container li a:hover {
    color: #fff;
    background: #939393;
}
.checkbox__container li ul.dropdown {
    min-width: 100%; /* Set width of the dropdown */
    background: #f2f2f2;
    display: none;
    position: absolute;
    z-index: 999;
    left: 0;
}
.checkbox__container li:hover ul.dropdown {
    display: block; /* Display the dropdown */
    background-color: #d5dbdb;
    border: 2px solid #008296;
}
.checkbox__container li ul.dropdown li {
    display: block;
}

/* The container */
.container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  
  background-color:#d5dbdb;
  border: 2px solid #d5dbdb;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  
  height: 25px;
  width: 25px;
  background-color: transparent;
  
  border: 2px solid #008296;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: #ccc; 
}

/* When the checkbox is checked, add a blue background */
.container input:checked ~ .checkmark {
  color: #008296;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  
  border: 2px solid #008296;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
